@import url(https://fonts.googleapis.com/css2?family=Black+Ops+One&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-image: linear-gradient(rgba(0, 0, 0, 1), rgba(90, 0, 0, 1), black 95%);
	background-attachment: fixed;
}

* {
	text-decoration: none;
	-webkit-tap-highlight-color: transparent;
	margin: 0;
	color: rgb(255, 255, 255);
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.db-reference {
	display: flex;
	align-items: center;
	font-size: 0.5rem;
	padding: 0.5rem;
	position: fixed;
	top: 0;
	left: 0;
	text-shadow: 2px 2px 10px black;
}

.reference-logo-container {
	display: flex;
	align-items: center;
	margin-left: 0.5rem;
}

.reference-logo2 {
	height: 0.8rem;
	width: auto;
	-webkit-filter: drop-shadow(2px 2px 2px black);
	        filter: drop-shadow(2px 2px 2px black);
}

.reference-logo {
	height: 0.5rem;
	width: auto;
	margin-left: 0.25rem;
	-webkit-filter: drop-shadow(2px 2px 1px black);
	        filter: drop-shadow(2px 2px 1px black);
}

.page-name {
	margin: 1rem;
	margin-top: 2rem;
}

.search-form {
	padding-bottom: 1rem;
}

input:focus {
	outline: none;
}

.search-bar {
	color: black;
	border-radius: 10px 0 0 10px;
	padding: 0.5rem;
}

.search-btn {
	background-color: black;
	border-radius: 0 10px 10px 0;
	padding: 0.5rem;
}

.fa-search {
	color: red;
}

.follow-recipe-instructions {
	width: 80%;
	margin: auto;
	margin-bottom: 1rem;
}

.search-results {
	width: 100vw;
	padding-bottom: 5rem;
}

.error-container {
	margin: auto;
	width: 80%;
}

.error-message {
	color: red;
}

.cards {
	margin-bottom: 2rem;
}

.recipe-title {
	font-size: 1rem;
	margin-bottom: 0.5rem;
	font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.calories {
	margin-bottom: 2rem;
}

.ingredients-container {
	padding: 0 2rem;
}

.recipe-seperater {
	margin: 1rem auto;
	border-bottom: solid 2px white;
	width: 80vw;
}

.greeting-container {
	min-height: 100vh;
	max-height: -webkit-fit-content;
	max-height: -moz-fit-content;
	max-height: fit-content;
	display: flex;
	flex-direction: column;
	align-items: center;
	-webkit-perspective: 1000px;
	perspective: 1000px;
	-webkit-perspective-origin: -50% calc(0%);
	perspective-origin: 50% calc(0%);
}

.quote-container {
	position: relative;
	width: 80vw;
	min-height: 200px;
	max-height: -webkit-fit-content;
	max-height: -moz-fit-content;
	max-height: fit-content;
	display: flex;
	justify-content: center;
	flex-direction: row;
	align-items: center;
	margin: auto;
}

.fa-quote-left {
	position: absolute;
	top: 0;
	left: 0;
	font-size: 4rem;
	color: darkred;
	z-index: -1;
}

.fa-quote-right {
	position: absolute;
	bottom: 0;
	right: 0;
	font-size: 4rem;
	color: darkred;
	z-index: -1;
}

.quote {
	font-family: cursive;
	font-weight: bolder;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
}

.scene {
	height: 50vw;
	position: relative;
	-webkit-transform-style: preserve-3d;
	transform-style: preserve-3d;
	width: 100vw;
	display: -ms-grid;
	display: grid;
	place-items: center;
	/* background-color: black; */
	/* backdrop-filter: blur(2px); */
}

.banner {
	position: absolute;
	display: flex;
	-webkit-transform-style: preserve-3d;
	transform-style: preserve-3d;
	-webkit-animation: rotate 20s infinite linear;
	animation: rotate 20s infinite linear;
}

