.panel {
	position: absolute;
	-webkit-transform: translate(-50%, -50%) rotateY(var(--angle)) translateZ(22.8vw);
	transform: translate(-50%, -50%) rotateY(var(--angle)) translateZ(22.8vw);
	width: 6vw;
	height: 15vw;
	overflow: hidden;
}

.panel::before {
	position: absolute;
	left: var(--left);
	content: 'Welcome to Take Charge!';
	font-size: 10vw;
	font-family: 'Black Ops One', cursive;
	width: -webkit-max-content;
	width: max-content;
	color: #000000;
	text-shadow: 0px 0px 3vw red, 0px 0px 3vw red;
	/* animation: blink 2s linear infinite; */
}

.panel:nth-child(1) {
	--left: 0vw;
	--angle: 0deg;
}

