.trainers-page {
	height: 100vw;
	padding: 2rem;
}

.trainer-links {
	height: 70vw;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	position: fixed;
	top: 50%;
	left: 50%;
	-webkit-transform: translateX(-50%) translateY(-50%);
	        transform: translateX(-50%) translateY(-50%);
}

.rapidAPI-logo {
	height: 1rem;
	width: auto;
	-webkit-filter: drop-shadow(2px 2px 2px black);
	        filter: drop-shadow(2px 2px 2px black);
}

.body-parts-container {
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
	margin: 2rem auto;
}

.header-container {
	margin: 1rem;
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

.body-part-btn {
	width: 25vw;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	font-size: 1rem;
	border: solid 2px black;
	background-image: linear-gradient(-45deg, black, rgba(90, 0, 0, 1), black);
	border-radius: 10px;
	margin: 0.5rem;
	padding: 1rem;
}

.results-container {
	margin: auto;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	width: 80vw;
	margin-bottom: 5rem;
}

.single-exercise {
	margin: 2rem;
	width: 80vw;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
}

.gifs {
	width: 70vw;
	height: auto;
	border-radius: 20px;
	border: solid 3px black;
}

figcaption {
	font-weight: bolder;
	font-size: 1rem;
	font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.fa-facebook-square {
	color: blue;
}

.hidden-menu-container {
	width: 80vw;
	min-height: 100vw;
	position: fixed;
	top: 1rem;
	margin: auto 2rem;
	background-color: black;
	display: grid;
}

.container {
	text-align: center;
	width: 100vw;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	margin: 0;
	/* padding-bottom: 5rem; */
}

.nav-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	position: fixed;
	bottom: 0rem;
	background-image: linear-gradient(rgba(0, 0, 0, 0), black 25%);
	padding-top: 1rem;
	padding-bottom: 1rem;
}

.icon-container {
	width: 100vw;
	padding: 1rem 0;
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
	align-items: center;
}

.nav-icon {
	color: rgb(0, 0, 0);
	-webkit-filter: drop-shadow(0px 0px 2px rgb(255, 0, 0));
	        filter: drop-shadow(0px 0px 2px rgb(255, 0, 0));
	-webkit-animation: blink 2s linear infinite;
	        animation: blink 2s linear infinite;
}

.logo {
	height: 5rem;
	width: auto;
	-webkit-filter: drop-shadow(0px 0px 2px rgb(255, 0, 0));
	        filter: drop-shadow(0px 0px 2px rgb(255, 0, 0));
	-webkit-animation: blink 2s linear infinite;
	        animation: blink 2s linear infinite;
}

.hidden-menu-btn {
	color: rgb(133, 0, 0);
	-webkit-filter: drop-shadow(0px 0px 1px rgb(255, 0, 0));
	        filter: drop-shadow(0px 0px 1px rgb(255, 0, 0));
	width: 70vw;
	position: fixed;
	bottom: 1rem;
	left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
	-webkit-animation: blink 2s linear infinite;
	        animation: blink 2s linear infinite;
}

@-webkit-keyframes blink {
	0% {
		opacity: 0;
	}
	50% {
		opacity: 0.5;
	}
	0% {
		opacity: 1;
	}
}

@keyframes blink {
	0% {
		opacity: 0;
	}
	50% {
		opacity: 0.5;
	}
	0% {
		opacity: 1;
	}
}

